.mp-pagination {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
a.num-rs-item {
    color: #6e6e6e;
    text-decoration: none;
    padding: 5px 10px;
    background-color: transparent;
    transition: 300ms;
    &:hover {
        color: #fff;
        background-color: #00aeef;
        transition: 300ms;
    }
}
a.num-rs-item.active {
    color: #fff;
    text-decoration: none;
    background-color: #00aeef;
}
.num-display {
    color: #6e6e6e;
    font-size: 16px;
}
.page-controller {
    display: flex;
    align-items: center;
    justify-content: center;
}
.pc-num-indicator {
    background-color: #ccceda;
    padding: 5px;
    margin: 5px;
}
a.pc-bb {
    width: 25px;
    margin: 5px;
}
a.pc-bb-single {
    width: 10px;
    margin: 5px;
}