.edit-head {
    background-color: #00aeef;
    padding: 20px;
    color: #fff;
    font-weight: 600;
}
.sos-edit-profile-wrap {
    padding: 20px;
}
.input-flex {
    display: flex;
    align-items: center;
    width: 100%;
}
input.form-control.input-custom {
    width: 80%;
}
i.ico-sos-fb {
    width: 40px;
    height: 40px;
    color: #445e9c;
    border: 2px solid #445e9c;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    border-radius: 60px;
    font-size: 20px;
}
