.set-size {
    font-size: 10em;
    margin-bottom: 60px; }
    @media (max-width: 500px) {
      .set-size {
        display: flex;
        justify-content: center; } }
  
  .charts-container:after {
    clear: both;
    content: "";
    display: table; }
  
  .pie-wrapper {
    height: 1em;
    width: 1em;
    float: left;
    margin: 15px;
    position: relative; }
  
  .pie-wrapper:nth-child(3n+1) {
    clear: both; }
  
  .pie-wrapper .pie {
    height: 100%;
    width: 100%;
    clip: rect(0, 1em, 1em, 0.5em);
    left: 0;
    position: absolute;
    top: 0; }
  
  .pie-wrapper .pie .half-circle {
    height: 100%;
    width: 100%;
    border: 8px solid #3498db;
    border-radius: 50%;
    clip: rect(0, 0.5em, 1em, 0);
    left: 0;
    position: absolute;
    top: 0; }
  
  .pie-wrapper .label {
    background: #34495e;
    border-radius: 50%;
    bottom: 0.4em;
    color: #686c6d !important;
    cursor: default;
    display: block;
    font-size: 14px;
    left: 0.4em;
    line-height: 2em;
    position: absolute;
    right: 0.4em;
    text-align: center;
    top: 2em; }
  
  span.percent {
    color: #1b8fd4;
    font-size: 32px; }
  
  .pie-wrapper .label .smaller {
    color: #161616;
    font-size: 0.45em;
    padding-bottom: 20px;
    vertical-align: super; }
  
  .pie-wrapper .shadow {
    height: 100%;
    width: 100%;
    border: 8px solid #bdbec0;
    border-radius: 50%; }
  
  .pie-wrapper.style-2 .label {
    background: none;
    color: #7f8c8d; }
  
  .pie-wrapper.style-2 .label .smaller {
    color: #161616; }
  
  .pie-wrapper.progress-30 .pie .right-side {
    display: none; }
  
  .pie-wrapper.progress-30 .pie .half-circle {
    border-color: #3498db; }
  
  .pie-wrapper.progress-30 .pie .left-side {
    -webkit-transform: rotate(108deg);
    transform: rotate(108deg); }
  
  .pie-wrapper.progress-60 .pie {
    clip: rect(auto, auto, auto, auto); }
  
  .pie-wrapper.progress-60 .pie .right-side {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg); }
  
  .pie-wrapper.progress-60 .pie .half-circle {
    border-color: #9b59b6; }
  
  .pie-wrapper.progress-60 .pie .left-side {
    -webkit-transform: rotate(216deg);
    transform: rotate(216deg); }
  
  .pie-wrapper.progress-90 .pie {
    clip: rect(auto, auto, auto, auto); }
  
  .pie-wrapper.progress-90 .pie .right-side {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg); }
  
  .pie-wrapper.progress-90 .pie .half-circle {
    border-color: #e67e22; }
  
  .pie-wrapper.progress-90 .pie .left-side {
    -webkit-transform: rotate(324deg);
    transform: rotate(324deg); }
  
  .pie-wrapper.progress-4 .pie .right-side {
    display: none; }
  
  .pie-wrapper.progress-4 .pie .half-circle {
    border-color: #01aef0; }
  
  .pie-wrapper.progress-4 .pie .left-side {
    -webkit-transform: rotate(4deg);
    transform: rotate(4deg); }
  
  .pie-wrapper.progress-75 .pie {
    clip: rect(auto, auto, auto, auto); }
  
  .pie-wrapper.progress-75 .pie .right-side {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg); }
  
  .pie-wrapper.progress-75 .pie .half-circle {
    border-color: #8e44ad; }
  
  .pie-wrapper.progress-75 .pie .left-side {
    -webkit-transform: rotate(270deg);
    transform: rotate(270deg); }
  
  .pie-wrapper.progress-95 .pie {
    clip: rect(auto, auto, auto, auto); }
  
  .pie-wrapper.progress-95 .pie .right-side {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg); }
  
  .pie-wrapper.progress-95 .pie .half-circle {
    border-color: #01aef0; }
  
  .pie-wrapper.progress-95 .pie .left-side {
    -webkit-transform: rotate(342deg);
    transform: rotate(342deg); }
  
  .pie-wrapper--solid {
    border-radius: 50%;
    overflow: hidden; }
  
  .pie-wrapper--solid:before {
    border-radius: 0 100% 100% 0%;
    content: "";
    display: block;
    height: 100%;
    margin-left: 50%;
    -webkit-transform-origin: left;
    transform-origin: left; }
  
  .pie-wrapper--solid .label {
    background: transparent; }
  
  .pie-wrapper--solid.progress-65 {
    background: -webkit-gradient(linear, left top, right top, color-stop(50%, #e67e22), color-stop(50%, #34495e));
    background: linear-gradient(to right, #e67e22 50%, #34495e 50%); }
  
  .pie-wrapper--solid.progress-65:before {
    background: #e67e22;
    -webkit-transform: rotate(126deg);
    transform: rotate(126deg); }
  
  .pie-wrapper--solid.progress-25 {
    background: -webkit-gradient(linear, left top, right top, color-stop(50%, #9b59b6), color-stop(50%, #34495e));
    background: linear-gradient(to right, #9b59b6 50%, #34495e 50%); }
  
  .pie-wrapper--solid.progress-25:before {
    background: #34495e;
    -webkit-transform: rotate(-270deg);
    transform: rotate(-270deg); }
  
  .pie-wrapper--solid.progress-88 {
    background: -webkit-gradient(linear, left top, right top, color-stop(50%, #3498db), color-stop(50%, #34495e));
    background: linear-gradient(to right, #3498db 50%, #34495e 50%); }
  
  .pie-wrapper--solid.progress-88:before {
    background: #3498db;
    -webkit-transform: rotate(43.2deg);
    transform: rotate(43.2deg); }