.act-feed-box {
    margin: 10px auto;
    background-color: #1c1f26;
}
.af-box-head {
    background-color: #262a35;
    padding: 10px;
}
h6.af-title {
    color: #fff;
    text-align: center;
}
span.af-col {
    color: #00a8e9;
}
ul#af-tab {
    background-color: #1c1f26;
}
.nav-pills .nav-link.af-nav.active {
    color: #90c63c;
    border-top: 2px solid #90c63c;
    background-color: transparent;
    border-radius: 0;
    padding: 10px 10px;
    font-size: 12px;
}
.nav-pills .nav-link.af-nav {
    color: #fff;
    border-top: 2px solid transparent;
    background-color: transparent;
    border-radius: 0;
   padding: 10px 10px;
   font-size: 12px; 
}
.offer-box {
    background-color: #262a35;
    padding: 10px;
    position: relative;
}
.offer-flex {
    display: flex;
}
.offer-ico {
    margin-right: 10px;
}
i.fa.fa-info-circle.of-ico {
    color: #fff;
}
.offer-title {
    color: #838488;
    font-weight: 600;
}
p.p-off-desc {
    color: #838488;
    text-align: justify;
    font-size: 13px;
}
span.close-offer {
    color: #838488;
    position: absolute;
    top: 0;
    right: 0;
    margin-right: 10px;
    cursor: pointer;
}
.af-user-flex {
    display: flex;
    align-items: flex-start;
}
.af-user-avatar {
    width: 25%;
    margin-right: 10px;
    position: relative;
}
img.img-fluid.af-ico {
    width: 15px;
    position: absolute;
    bottom: 0;
    right: 0;
}
img.img-fluid.img-af-ava {
    border-radius: 60px;
}
.af-user-name {
    color: #00aeef;
    font-size: 14px;
}
.af-user-box {
    padding: 10px;
    background-color: #262a35;
    margin: 5px;
    position: relative;
}
.af-user-act {
    width: 75%;
}
.af-button-wraper {
    display: flex;
   margin: 7px auto;
}
.btn-af-co.btn-sm {
    font-size: 12px;
    padding: 10px;
    background-image: linear-gradient(to right, #766fb2, #30b1ea);
    color: #fff;
    border-radius: 0;
    margin-right: 5px;
    text-align: center; 
    cursor: pointer;
}
.btn-af-re.btn-sm {
    cursor: pointer;
    font-size: 12px;
    padding: 10px;
    background-image: linear-gradient(to right, #f8931d, #ec5d25);
    color: #fff;
    border-radius: 0;
    text-align: center;    
}
.af-act-item {
    color: #fff;
    font-size: 10px;
    background-color: #303440;
    padding: 5px;
    border-left: 4px solid #1c1f26;
}
span.af-red {
    color: red;
}
span.af-green {
    color: green;
}
.af-c-btn-wraper {
    display: flex;
    justify-content: space-around;
}
i.af-ico-act {
    color: #fff;
    cursor: pointer;
}
i.af-ico-act.active {
    color: #00aeef;
}
.af-box-body {
    padding-bottom: 1px;
}
.top-trader-title {
    background-color: #262a35;
    padding: 10px;
}
.tt-month {
    color: $text-color;
    text-align: center;
}
.top-trade-box {
    margin: 10px auto;
    background-color: #1c1f26;
    padding-bottom: 1px;
}
.tt-username-flex {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-around;
    background-color: rgba(128, 128, 128, .6);
    align-items: center;
}
.top-trade-img {
    position: relative;
}
.tt-ver {
    width: 10px;
}
.tt-username {
    color: #fff;
    font-size: 12px;
}
.tt-fol {
    font-size: 10px;
    color: #00aeef;
}
.top-trade-flex {
    display: flex;
    align-items: flex-end;
    background-color: #262a35;
}
.top-trade-img {
    width: 40%;
}
.tt-cr-flex {
    display: flex;
    margin: 5px;
}
.tt-cr-copy {
    font-size: 12px;
    color: $text-color;
    margin-right: 5px;
}
.tt-cr-reverse {
    font-size: 12px;
    color: $text-color;
}
.top-trade-content {
    width: 60%;
}
.ul-rd{
  display: flex;
  padding: 0;
  justify-content: space-around;
}
li.li-rd{
  list-style-type: none;
  margin: 0;
  padding: 0 5px;
  font-size: 12px;
}
li.li-rd:not(:last-child) {
  border-right: 1px solid #b9bdc0;
}
.rd-percent {
    color: #fff;
    font-size: 14px;
    font-weight: 600;
}
.rd-status {
    color: #fff;
    font-size: 10px;
}
.tt-core-flex {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.tt-core-copy {
    color: #fff;
    font-size: 12px;
    display: flex;
    justify-content: center;
    width: 50%;
    align-items: center;
    background-image: linear-gradient(to right, #766fb2, #30b1ea);
    padding: 5px;
    cursor: pointer;
}
.tt-core-reverse {
    color: #fff;
    font-size: 12px;
    display: flex;
    justify-content: center;
    width: 50%;
    align-items: center;
    background-image: linear-gradient(to right, #f8931d, #ec5d25);
    padding: 5px;
    cursor: pointer;
}
.top-trade-body {
    margin: 5px;
}
.suggest-trader-box {
    margin: 10px auto;
    background-color: #1c1f26;
    padding-bottom: 1px;
}

.st-head-box {
    background-color: #262a35;
    padding: 10px;
}
.st-trade-flex {
    display: flex;
    background-color: #262a35;
    padding: 10px;
    border-radius: 5px;
}
.st-img {
    width: 25%;
    margin-right: 10px;
}
.st-trader-body {
    margin: 5px;
    position: relative;
}
.st-name {
    color: #00aeef;
    font-weight: 600;
}
.st-fol {
    color: #fff;
    font-size: 10px;
    display: flex;
    justify-content: space-between;
}
.st-fol-logo {
    width: 10px;
}
img.img-fluid.img-st {
    border-radius: 60px;
}
button.btn.btn-st-fol.btn-sm {
    background-color: #8dc73f;
    color: #fff;
    position: absolute;
    top: 3px;
    right: 3px;
}
a.view-suggest {
    font-size: 14px;
    color: #02a5e4;
}
.other-suggest {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}
.af-time-date {
    color: #fff;
    font-size: 10px;
    position: absolute;
    top: 2px;
    right: 5px;
}