.trading-performance-box {
    margin: 10px auto;
}
.tf-head-box {
    background-color: #ccceda;
    padding: 10px;
}
h6.tf-title {
    color: #fff;
    text-align: center;
}
span.tf-col {
    color: #575759;
}
.tf-body {
    background-color: #fff;
    padding: 20px;
}
.tf-acc-type {
    font-size: 14px;
    color: #00aeef;
    margin-bottom: 10px;
}
.num-tf-acc {
    font-size: 14px;
    margin-left: 10px;
}
.ul-tf {
  display: flex;
  padding: 0;
  justify-content: center;
  margin-top: 10px;
}
li.li-tf {
  list-style-type: none;
  margin: 0;
  padding: 0 5px;
  font-size: 16px;
  text-align: center;
}
li.li-tf:not(:last-child) {
  border-right: 1px solid #00aeef;
}
hr.hr-tf {
    border-top: 1px solid #00aeef;
    margin: 10px auto;
}
span.tf-bold {
    font-weight: 600;
}
.tf-link-footer-wraper {
    background-color: #ccceda;
    padding: 20px;
    display: flex;
    flex-direction: column;
    text-align: center;
}
a.tf-link {
    color: #01aef0;
    text-decoration: none;
}
