.pro-right-box {
    background-color: #fff;
    padding-bottom: 20px;
    margin-bottom: 20px;
}
.prb-image-wrap {
    width: 60%;
    padding: 20px 0 10px;
    margin: 0 auto;
}
.prb-name {
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    color: #6a6b6d;
}
.prb-bio-wrap {
    padding: 20px;
}
.bio-title {
    color: #706f74;
    font-weight: 600;
    font-size: 16px;
}
p.bio-desc {
    font-size: 14px;
    text-align: justify;
    color: #7c7c7e;
}
.prb-social {
    display: flex;
    justify-content: space-around;
    width: 60%;
    margin: 0 auto;
}
a.prb-social-link-fb {
    color: #0573be;
    border: 1px solid #0573be;
    height: 25px;
    width: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 60px;
    text-decoration: none;
}
a.prb-social-link-tw {
    color: #01aef0;
    border: 1px solid #01aef0;
    height: 25px;
    width: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 60px;
    text-decoration: none;
}
a.prb-social-link-ins {
    color: #964b9d;
    border: 1px solid #964b9d;
    height: 25px;
    width: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 60px;
    text-decoration: none;
}
a.prb-social-link-lin {
    color: #293a8c;
    border: 1px solid #293a8c;
    height: 25px;
    width: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 60px;
    text-decoration: none;
}
a.btn.btn-edit-prb {
    background-image: linear-gradient(to right, #3db24a, #24aadf);
    color: #fff !important;
    margin: 0 5px;
    border-radius: 5px;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
i.fa.fa-pencil-square-o.edit-ico {
    margin-right: 5px;
}
.edit-prb-btn-wrap {
    width: 65%;
    margin: 20px auto;
}
.tp-right-box {
    background-image: linear-gradient( #0e76bd, #01aef0);
    margin-bottom: 20px;
}
.trb-head {
    padding: 15px;
    border-bottom: 1px solid #fff;
}
.trb-title {
    color: #fff;
    font-size: 16px;
    font-weight: 600;
}
.trb-caption {
    font-size: 14px;
    color: #fff;
}
.trb-body {
    padding: 15px;
}
.trb-item {
    font-size: 14px;
    color: #fff;
}
.trb-unit {
    font-size: 14px;
    color: #fff;
}
.trb-items-flex {
    display: flex;
    justify-content: space-between;
}
ul.ul-trb {
    display: flex;
    padding: 0;
    justify-content: space-around;
    margin-top: 10px;
}
li.li-trb:not(:last-child) {
    border-right: 1px solid #fff;
    padding-right: 15px;
}
li.li-trb {
    list-style-type: none;
    margin: 0;
    padding: 0 5px;
    font-size: 16px;
    text-align: center;
    color: #fff;
}
.li-trb-num {
    font-size: 14px;
    font-weight: 600;
}
.li-trb-item {
    font-size: 14px;
}
.trb-cr {
    text-align: center;
}
.trb-items-cr-flex {
    display: flex;
    justify-content: space-around;
}
.trb-cr-num {
    font-size: 30px;
    color: #fff;
}
.trb-cr-text {
    color: #fff;
    font-size: 14px;
}
.trb-btn-cr {
    display: flex;
    justify-content: space-around;
    margin: 20px auto;
}
button.btn.btn-cr.btn-sm {
    color: #fff;
    background-color: transparent;
    border: 2px solid #fff;
}
hr.hr-trb {
    border-top: 1px solid #fff;
}
.pamm-right-box {
    background-image: linear-gradient( #2bb673, #1277bb, #00adef);
    margin-bottom: 20px;
}
.no-data {
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 30px;
}