.competition-desc-box {
    margin: 20px;
}
.cd-head-box {
    padding: 20px;
    background-color: #cbced5;
}
.cd-name {
    color: #6b6f72;
    margin-left: 20px;
    font-weight: 600;
}
.cd-body-box {
    background-color: #fff;
    padding: 40px;
}
p.cd-desc {
    font-size: 14px;
    color: #636363;
}
.carousel-competition-wraper {
    margin: 40px;
}
.carousel-title {
    text-align: center;
    padding: 20px;
    background-color: #00aeef;
    color: #fff;
    font-weight: 600;
}
.carousel-flex {
    display: flex;
    width: 100%;
}
.car-img {
    width: 70%;
}
.car-caption {
    background-color: #fff;
    width: 30%;
    padding: 20px;
}
.car-cap-title {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 15px;
    text-decoration: none;
    color: #000;
    &:hover {
        text-decoration: none;
        color: #000;
    }
}
p.art-desc {
    font-size: 14px;
}
a.art-read-more {
    font-size: 14px;
    text-align: right;
    display: inherit;
}
.art-dp-wrap {
    margin-top: 20px;
}
.door-prize {
    color: #5a5a5c;
    font-size: 14px;
}
.dp-price {
    font-size: 24px;
    font-weight: 600;
    color: #1076b6;
}
span.dp-yellow {
    font-size: 14px;
    color: #e88f31;
    font-weight: 400;
}
.car-regist {
    padding: 20px;
    text-align: center;
    background-color: #cbced5;
    font-weight: 600;
    color: #545456;
}
span.carousel-control-prev-icon.prev-bl {
    background-image: url(../img/prev-bl.png);
    width: 40px;
    height: 40px;
}
span.carousel-control-next-icon.next-bl {
    background-image: url(../img/next-bl.png);
    width: 40px;
    height: 40px;
}
.comp-list-outer {
    margin: 40px;
}
.sub-comp-list-title {
    background-color: #cbced5;
    padding: 20px;
    color: #6d6e70;
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 15px;
}
.cl-head-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 10px;
    background-color: #fff;
}
.img-cl {
    width: 50%;
}
.cl-date {
    width: 50%;
    text-align: right;
    color: #01aef0;
}
.cl-body-content {
    padding: 10px;
    background-color: #fff;
}
.cl-sub-title-col {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 10px;
}
p.cl-sub-desc {
    font-size: 14px;
}
a.cl-link-sub {
    font-size: 14px;
    color: #01aef0;
}
.cl-regist-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.cl-reg-item {
    text-align: center;
    font-size: 12px;
    width: 50%;
    border: 1px solid #acadb1;
    padding: 10px;
}
.cl-footer-box {
    background-color: #f3f3f5;
}
span.sub-cl-reg-tiem {
    color: #9fa0a2;
}
.cl-box {
    margin-bottom: 20px;
}
.cl-body-img {
    position: relative;
}
.lv-comp {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    padding: 10px;
    color: #fff;
    font-size: 12px;
    cursor: pointer;
}
.cd-name-table {
    color: #fff;
    margin-left: 20px;
    font-weight: 600;
}
.cd-head-box-table {
    padding: 20px;
    background-color: #00aeef;
}
th.title-th {
    border-top-color: transparent;
}
.profit-box {
    position: relative;
    bottom: 7px;
    display: flex;
    width: 100%;
    justify-content: center;
}
.profit {
    margin-right: 10px;
    margin-top: 8px;
}
th.ptt-30 {
    padding-top: 30px;
}
td.ptt-40 {
    padding-top: 40px;
}
.trader-col-wrap {
    display: flex;
}
.trader-img-box {
    width: 45px;
    height: 45px;
    margin-right: 10px;
}
img.trader-img {
    border-radius: 60px;
}
.trade-name-tp {
    color: #01aef0;
    text-align: left;
}
.trade-nat {
    font-size: 14px;
    font-weight: 400;
    text-align: left;
}
.profit-chart {
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.tab-pagination-cl {
    margin-top: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #7f7f7f;
}
.page-number-cl {
    margin-left: 18px;
    margin-right: 18px;
    margin-bottom: 0px;
    background-color: #f3f3f5;
    padding: 5px;
}
.next-cl {
    margin-right: 15px;
}
.prev-cl {
    margin-left: 15px;
}
