.nav-sub-page-fb {
    background-color: #01aef0;
    padding: 10px;
    color: #fff;
    font-weight: 600;
}
.fb-head {
    background-color: #d1d2d4;
    padding: 15px;
}
.fb-title {
    color: #38383a;
    font-size: 16px;
    font-weight: 600;
    text-align: center;
}
.fb-body {
    background-color: #fff;
    padding: 15px;
}
.fb-img {
    width: 70%;
    margin: 10px auto;
}
img.img-fluid.img-fb {
    border-radius: 110px;
}
p.p-fb-desc {
    color: #6d6c71;
    font-size: 14px;
    text-align: justify;
}
a.fb-link {
    color: #00a8e9;
    text-align: center;
    display: inherit;
}
.fb-box {
    margin: 15px;
}