.det-post-box {
    margin: 10px;
    background-color: #fff;
}
.det-post-head-box {
    position: relative;
}
.dp-ava-flex {
    display: flex;
    align-items: center;
    width: 100%;
}
.dp-ava-wrap {
    width: 10%;
    margin: 10px;
    text-align: center;
}
img.img-fluid.img-dp-ava {
    width: 75px;
    border-radius: 60px;
    height: 75px;
    overflow: hidden;
}
.dp-name {
    color: #0e7ac5;
    font-weight: 600;
    font-size: 18px;
}
.dp-fol,.dp-date {
    font-size: 14px;
    color: #acacae;
}
.dp-category {
    position: absolute;
    top: 0;
    right: 0;
    background-image: linear-gradient(to right, #7c4098, #00ace9);
    padding: 10px 30px;
    border-bottom-left-radius: 25px;
    color: #fff;
}
.det-post-body-box {
    border-top: 2px solid lightgray;
    padding: 20px;
}
h4.dp-title {
    text-align: center;
}
.dp-body-img {
    width: 60%;
    margin: 40px auto;
}
article.dp-article {
    width: 85%;
    margin: 0 auto;
}
.dp-comment-box {
    width: 85%;
    margin: 0 auto;
    background-color: #f1f1f3;
}
.dpc-head {
    padding: 10px;
}
.dpc-icon-wraper {
    display: flex;
    justify-content: space-around;
}
i.cpd-ico {
    color: #808185;
    cursor: pointer;
}
a.dpc-hid {
    color: #93c24e;
}
.dpc-uc-flex {
    display: flex;
    align-items: center;
    width: 90%;
    margin: 0 auto;
}
.dpc-ava {
    width: 10%;
    margin-right: 10px;
}
img.img-fluid.dpc-img {
    border-radius: 60px;
}
.dpc-name {
    color: #13aaa1;
    font-weight: 600;
    font-size: 16px;
}
.cpd-mess {
    color: #5d5b5c;
    font-size: 14px;
}
.dpc-user-comment {
    margin: 10px;
}
.dpc-form-footer {
    padding: 20px;
    background-color: #e6e9f0;
}
.det-post-body-box-v2 {
    padding: 20px;
}
hr.hr-det-post {
    border-top: 2px solid lightgray;
    margin: 10px;
}
h4.dp-title-left {
    text-align: left;
}
.side-detail-box {
    padding: 20px;
}
.dp-body-img-v2 {
    width: 90%;
    margin: 40px auto;
}
.dpc-ava-v2 {
    width: 25%;
    margin-right: 10px;
}
.df-title {
    background-color: #01aef0;
    color: #fff;
    text-align: center;
    padding: 20px;
}
.form-group.col-md-12.reg-flex {
    display: flex;
    width: 100%;
}
input.form-control.input-custom-wsd {
    border-color: transparent;
    border-bottom: 1px solid;
    border-radius: 0;
    background-color: transparent;
}
.reg-ico-wraper {
    width: 10%;
}
.regist-form-wrap {
    background-color: #f1f1f3;
    padding: 30px;
}
.btn-submit-wrap-wsd {
    text-align: center;
}
button.btn.btn-submit-wsd {
    color: #fff;
    background-image: linear-gradient(to right, #12a89d, #2bb673);
    border-color: transparent;
}
button.btn.btn-submit-wsd:focus {
    color: #fff;
    background-image: linear-gradient(to right, #12a89d, #2bb673);
    border-color: transparent;
}
.detail-table-box {
    background-color: #f1f1f3;
    margin-top: 20px;
}
.pagination-wraper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}
img.img-pg-ico {
    width: 25px;
    margin: 5px;
}
img.img-pg-ico-2 {
    width: 10px;
    margin: 5px;
}
.pg-num {
    background-color: #58585a;
    color: #fff;
    margin: 5px;
    padding: 5px 10px;
}
.regist-date-flex {
    background-color: #f1f1f3;
    display: flex;
    justify-content: space-around;
    padding: 10px;
}
.regist-box {
    text-align: center;
}
.rg-date-one {
    color: #6b6c70;
    font-size: 14px;
    font-weight: 600;
}
.rg-date-two {
    font-size: 14px;
    color: #828186;
}
.view-comp-list-box {
    padding: 20px;
    text-align: center;
}
.view-comp-list-box {
    margin: 20px;
    padding: 15px;
    text-align: center;
    border: 1px solid #21a8e2;
}
a.view-cl-link {
    color: #21a8e2;
}