.plt-box {
    margin: 20px;
}
.plt-top-btn {
    text-align: right;
}
button.btn.btn-plt-rev {
    background-color: #cbced5;
    color: #727679;
}
.plt-profile-box {
    width: 100%;
    display: flex;
}
.plt-ava {
    width: 50%;
}
.plt-profile {
    width: 50%;
    padding: 20px;
    background-color: #fff;
}
.plt-name {
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 15px;
    color: #6d6e70;
}
.plt-item {
    font-size: 17px;
    color: #6d6e70;
    margin-bottom: 5px;
}
.plt-btn-follow {
    text-align: right;
    margin-top: 20px;
}
.trio-chart-box-pc-1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: #319ed7;
    padding: 10px;
}
.trio-chart-box-pc-2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: #1badad;
    padding: 10px;
}
.trio-chart-box-pc-3 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: #814a9d;
    padding: 10px;
}
.ct-balance {
    color: #fff;
    font-size: 16px;
}
.ct-balance-num {
    color: #fff;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 1px;
}
.chart-number {
    text-align: right;
    color: #fff;
}
img.tp-ico {
    width: 10px;
    margin-right: 5px;
}
.pr-mini-title {
    color: #6d6e72;
    text-align: center;
    margin-bottom: 30px;
    padding: 20px;
}
.col-md-12.plt-chart-bg {
    background-color: #fff;
}
.chart-container.plt-chart-container {
    background-color: #3a3a3c;
}
.ac-box {
    background-color: #fff;
    padding: 20px;
    margin-bottom: 20px;
}
.ac-head-box {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}
.ac-date-box {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 33%;
}
.ad-date-unit {
    background-color: #f3f3f5;
    color: #818183;
    padding: 10px;
    height: 40px;
    width: 45%;
    text-align: right;
}
.ac-button-box {
    width: 33%;
}
.ul-ac-btn {
    padding: 0;
    display: flex;
    list-style: none;
    justify-content: space-around;
    flex-wrap: wrap;
}
.li-ac-btn {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #cbced5;
    cursor: pointer;
    background-color: #cbced5;
}
a.ac-link-btn {
    color: #878a8f;
    text-decoration: none;
}
.ac-select {
    width: 33%;
}
.form-group.fg-ac {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 0;
}
form.form-ac {
    display: flex;
    justify-content: flex-end;
}
.form-group.fg-ac {
    width: 40%;
}
.form-control.form-ac {
    background-color: #01aef0;
    color: #fff;
}
.credit-box {
    display: flex;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap;
    margin-top: 20px;
}
.credit-facility {
    display: flex;
    justify-content: space-between;
    width: 30%;
    color: #6b6c70;
}
h5.ac-sum {
    color: #6b6c70;
    margin-top: 20px;
    margin-bottom: 20px;
}
.ac-sum-content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}
.ac-sum-content-box {
    width: 30%;
}
.ac-sum-unit {
    display: flex;
    justify-content: space-between;
    color: #6b6c70;
}
.nav-tabs .nav-link.nav-plt.active {
    color: #01aef0;
    font-size: 16px;
    background-color: #fff;
    border-color: #dee2e6 #dee2e6 #fff;
}
.nav-tabs .nav-link.nav-plt {
    color: #6e6f73;
    font-size: 16px;
    background-color: #cbced5;
    border-color: #dee2e6 #dee2e6 #fff;
}
.tab-pane-plt {
    margin-top: 40px;
}
.row.justify-content-center.row-pb {
    margin-top: 40px;
}
.row.row-big-chart {
    margin: 0 auto;
    width: 1000px;
}
.row.row-table-ps {
    margin: 0 auto;
    width: 1000px;
}