h4.gal-title {
    color: #6d6e72;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
}
.gal-box {
    position: relative;
    margin: 10px;
    box-shadow: 0px 0px 5px 4px #a5a5a7;
    cursor: pointer;
}
.gal-date {
    position: absolute;
    top: 0;
    right: 0;
    background-color: rgba(128, 128, 128, .5);
    padding: 5px 20px;
    margin: 5px;
    border-radius: 20px;
}
i.fa.fa-picture-o.gal-ico {
    color: #fff;
    font-size: 14px;
}
.gal-img-wraper {
    position: relative;
}
.gal-caption {
    position: absolute;
    bottom: 0;
    background-color: rgba(128, 128, 128, .7);
    left: 0;
    right: 0;
    padding: 15px;
    color: #fff;
    opacity: 0;
    transition: 300ms;
}
.gal-box:hover .gal-caption {
    opacity: 1;
    transition: 300ms;
}