.notes-box {
    margin: 40px 20px;
    background-color: #fff;
    padding: 20px;
}
.notes-head {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}
.img-notes {
    margin-right: 5px;
}
img.img-fluid.note-ico {
    width: 20px;
}
.note-title {
    color: #00aeef;
    font-size: 16px;
    font-weight: 600;
}
.note-body-title {
    display: flex;
    align-items: center;
}
.nb-img {
    margin-right: 5px;
}
img.img-fluid.note-nb-ico {
    width: 15px;
}
.nb-title {
    font-size: 14px;
    color: #59595b;
    font-weight: 600;
}
.note-body {
   background-color: #f3f3f5;
   padding: 10px;
   margin-bottom: 20px;
}
p.note-desc {
    color: #6b6b6d;
    font-size: 13px;
    text-align: justify;
}
.note-body-desc {
    margin-top: 15px;
}
a.read-more {
    color: #00b0f8;
    font-size: 14px;
}
.note-content {
    background-color: #f3f3f5;
    padding: 20px;
    color: #828186;
}
.note-date {
    font-size: 14px;
    margin-bottom: 10px;
}
