.carousel-inner.carousel-inner-mp {
    height: 500px;
    overflow: hidden;
}
.profile-nav {
    padding: 20px;
    background-color: #cbced5 !important;
}
ul.nav-pills.nav-mp {
    background-color: #f3f3f5;
    padding: 15px;
}
.nav-pills .nav-link.nav-mp-unit.active {
    color: #01aef0;
    background-color: #f3f3f5;
}
.nav-pills .nav-link.nav-mp-unit {
    color: #6d6d6f;
    background-color: #f3f3f5;
    font-weight: 600;
    font-size: 14px;
}
.make-post-mp {
    background-color: #d1d2d4;
    padding: 10px;
    width: 75%;
    margin: 0 auto;
    border-radius: 5px;
    box-shadow: 0px 0px 8px 6px #dedede;
}
.tl-content-box-mp {
    margin: 10px auto;
    width: 75%;
}
.to-mp-box {
    background-color: #fff;
    padding: 20px;
}
.to-box-blue {
    background-color: #01aef0;
    padding: 10px;
    margin-bottom: 20px;
}
.to-title-wraper {
    display: flex;
    justify-content: space-between;
}
.to-title {
    color: #fff;
    font-size: 16px;
}
.to-status {
    color: #fff;
    font-size: 16px;
}
ul.ul-to-ud {
    display: flex;
    padding: 0;
    justify-content: space-around;
    margin-top: 10px;
}
li.li-to-ud {
    list-style-type: none;
    margin: 0;
    padding: 0 5px;
    font-size: 16px;
    text-align: center;
    color: #fff;
}
li.li-to-ud:not(:last-child) {
    border-right: 1px solid #fff;
    padding-right: 45px;
}
.user-item-2 {
    font-size: 22px;
    font-weight: 600;
}
span.to-years {
    font-size: 16px;
}
.to-bt-title {
    font-size: 18px;
    font-weight: 600;
    color: #fff;
}
.bt-content {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.bt-img-wrap {
    width: 30%;
    padding: 30px;
}
.bt-table-wrap {
    width: 70%;
    margin-top: 15px;
}

tr.tr-bt {
    border-bottom: 1px solid #fff;
    border-top: 1px solid #fff;
}
td.td-bt {
    font-size: 16px;
    color: #fff;
}
.to-box-cur {
    background-color: #01aef0;
    width: 50%;
    margin-right: 5px;
}
.to-box-tra {
    background-color: #6253a4;
    width: 50%;
    margin-left: 5px;
}
.to-box-flex {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}
.img-cur-wrap {
    width: 100px;
    display: flex;
    align-items: center;
    margin: 40px auto 20px;
}
img.img-fluid.img-ta-pr {
    width: 100px;
    display: flex;
    align-items: center;
    margin: 40px auto 20px;
}
.to-caption {
    text-align: center;
    color: #fff;
    font-size: 16px;
}
.to-sub-captio {
    text-align: center;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
}
.to-caption-wraper {
    margin-bottom: 20px;
}
ul.ul-ta {
    display: flex;
    padding: 0;
    justify-content: center;
    margin-top: 10px;
}
li.li-ta {
    list-style-type: none;
    margin: 0;
    padding: 0 5px;
    font-size: 16px;
    text-align: center;
    color: #fff;
}
li.li-ta:not(:last-child)  {
    border-right: 1px solid #fff;
    padding-right: 15px;
}
.ta-num {
    font-size: 16px;
    font-weight: 600;
}
.ta-time {
    font-size: 14px;
}
.to-box-purple {
    background-color: #6253a4;
    padding: 20px;
    margin-bottom: 20px;
}
.nav-pills .nav-link.nav-tp.active {
    color: #00b4f7;;
    background-color: #fff;
    border: 1px solid #fff;
    border-radius: 0;
}
.nav-pills .nav-link.nav-tp {
    color: #fff;
    background-color: #00b4f7;
    border: 1px solid #00b4f7;
    border-radius: 0;
}
.tab-table {
    margin: 20px;
}
.table-mp {
    background-image: linear-gradient(to right, #2d3192, #00adef);
}
th.ptt-30 {
    padding-top: 30px;
}
.trader-col-wrap {
    display: flex;
}
.trader-img-box {
    width: 45px;
    height: 45px;
}
img.trader-img {
    border-radius: 60px;
}
.trade-name-tp {
    color: #fff;
    font-size: 14px;
}
.trade-nat {
    color: #fff;
    font-size: 12px;
    font-weight: 400;
    text-align: left;
}
.profit-box {
    position: relative;
    bottom: 7px;
    display: flex;
    width: 100%;
    justify-content: center;
}
.profit {
    margin-right: 10px;
    margin-top: 7px;
}
th.title-th {
    color: #fff;
    font-size: 14px;
}
td.ptt-40 {
    padding-top: 40px;
    color: #fff;
    font-size: 12px;
}
.profit-chart {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
}
.profit-num {
    color: #fff;
    font-size: 12px;
}
.rating-tab {
    text-align: center;
    display: flex;
    flex-direction: column;
}
.rating-icons-table span {
    display: inline-block;
    width: 12px;
    height: 12px;
    background: transparent;
    border-radius: 50%;
    margin-right: 3px;
    border: 2px solid #fff;
}
.rating-icons-table span:after {
    content: "";
    display: block;
    height: 6px;
    width: 6px;
    background: #fff;
    border-radius: 50%;
    margin: 1px;
}
td.ptt-30 {
    padding-top: 30px;
    color: #fff;
}
button.btn.btn-invest-bg {
    background-color: transparent;
    border: 1px solid #fff;
    color: #fff;
    border-radius: 0;
}
.tab-pagination {
    margin-top: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #7f7f7f;
    margin-bottom: 15px;
}
p.page-number {
    margin-left: 18px;
    margin-right: 18px;
    margin-bottom: 0px;
    background-color: #ffffff;
    padding: 5px;
    color: #01aef0;
}
.prev {
    margin-left: 15px;
}
.next {
    margin-right: 15px;
}
.button-trade-tp {
    margin-top: 20px;
}
img.img-fluid.img-core {
    width: 35px;
    cursor: pointer;
}
.ea-name {
    color: #fff;
}