.notif-flex {
    width: 100%;
    display: flex;
    align-items: center;
}
.notif-ava {
    width: 10%;
    margin-right: 10px;
    border-radius: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}
img.img-fluid.img-not-ava {
    width: 50px;
    height: 50px;
    overflow: hidden;
    border-radius: 60px;
}
.notification-box {
    background-color: #fff;
    margin: 50px auto;
    box-shadow: 0px 0px 15px 5px #ccc;
}
.nt-name {
    color: #6d6e72;
    font-size: 14px;
    font-weight: 600;
}
span.notif-type, span.notif-mess {
    font-weight: 400;
}
.notif-date-flex {
    display: flex;
    align-items: center;
}
.notif-ico {
    margin-right: 10px;
    width: 5%;
}
.notif-time {
    font-size: 12px;
    color: #707072;
}
.notif-item.unread {
    padding: 10px 0;
    border-left: 4px solid #00aeef;
    background-color: #fff;
    cursor: pointer;
}
.notif-item {
    padding: 10px 0;
    background-color: #f9fafc;
    cursor: pointer;
    margin-bottom: 2px;
}