.analysis-box {
    margin: 40px 20px;
    background-color: #fff;
    padding: 20px;
}
.an-box-head {
    color: #00aeef;
    font-weight: 600;
    font-size: 18px;
}
.an-box-unit-img-wraper {
    position: relative;
}
.an-date {
    position: absolute;
    top: 0;
    color: #fff;
    font-size: 14px;
    background-color: rgba(128, 128, 128, .5);
    padding: 5px;
    border-radius: 10px;
}
a.an-title {
    color: #58585a;
    font-size: 16px;
    font-weight: 600;
}
p.an-desc {
    font-size: 14px;
    text-align: justify;
    margin-top: 10px;
    color: #969799;
}
.an-box-body-text {
    background-color: #f3f3f5;
    padding: 10px;
    position: relative;
}
.an-category {
    position: absolute;
    top: 0;
    right: 0;
    background-color: #01aef0;
    padding: 5px;
    color: #fff;
}
.an-box-title-wraper {
    margin-top: 30px;
}
.an-box-unit {
    position: relative;
    margin-bottom: 20px;
}
.an-overlay {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(1, 174, 240, .5);
    opacity: 0;
    transition: 300ms;
    &:hover {
      opacity: 1;
      transition: 300ms;

    }
}
a.detail-btn {
    color: #fff;
    padding: 10px 20px;
    border: 1px solid #fff;
    text-decoration: none;
}
.an-box-body {
    margin-top: 20px;
}
i.article-ico {
    color: #01aef0;
    margin-right: 5px;
}
.upload-title {
    color: #01aef0;
    font-size: 16px;
    font-weight: 600;
    padding: 20px;
    background-color: #f3f3f5;
}
.upload-form-box {
    padding: 20px;
}
.image-upload-box {
    padding: 20px;
}
.text-editor-area {
    padding: 20px;
}
.upload-btn {
    text-align: center;
}
button.btn.btn-submit {
    background-image: linear-gradient(to right, #24aadf, #3db24a);
    color: #fff;
    border-color: transparent;
}
img.img-fluid.ico-upload {
    width: 20px;
    margin-right: 5px;
}