a.pr-link-item.active {
    background-color: #7a8599;
    padding: 5px;
    color: #fff;
    border-radius: 5px;
}
.photo-album-box {
    margin: 40px 20px;
}
img.img-fluid.img-ico-photo {
    width: 20px;
}
.nav-tabs .nav-link.nav-album.active {
    color: #00aeef;
    background-color: #fff;
    border-color: #dee2e6 #dee2e6 #fff;
    border-radius: 0;
        img.img-fluid.img-ico-photo {
        filter: saturate(1);
    }
}
.nav-tabs .nav-link.nav-album {
    color: #6d6e72;
    background-color: #cbced5;
    border-color: #dee2e6 #dee2e6 #fff;
    border-radius: 0;
       img.img-fluid.img-ico-photo {
        filter: saturate(0);
    }
}
.tab-content.tab-photo {
    background-color: #fff;
}
.upload-ico-wrap {
    width: 100px;
    margin: 0 auto;
}
.photo-upload-box {
    padding: 40px;
}
img.img-fluid.img-ico-upl {
    cursor: pointer;
}
.pht-img-unit {
    position: relative;
}
.pht-date {
    position: absolute;
    top: 0;
    right: 0;
    color: #fff;
    background-color: rgba(128, 128, 128, .7);
    padding: 8px;
    font-size: 12px;
    border-radius: 15px;
}
.pht-title {
    color: #59595b;
    font-size: 14px;
    font-weight: 600;
    margin-top: 20px;
    display: inherit;
    text-decoration: none;
    &:hover {
        color: #59595b;
    }
}
p.pht-desc-txt {
    color: #8f8f91;
    text-align: justify;
    font-size: 13px;
}
.pht-unit-desc {
    background-color: #f3f3f5;
    padding: 10px;
    position: relative;
}
.pht-cat {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 12px;
    color: #fff;
    background-color: #00aeef;
    padding: 5px;
}
.photo-unit {
    margin: 10px;
}
.photo-box-inner {
    padding-top: 40px;
    padding-bottom: 40px;
}
.pht-box-detail {
    background-color: #fff;
    margin: 40px 20px;
}
.pd-head-box {
    padding: 20px;
    position: relative;
}
.img-pd-wrap {
    display: flex;
    align-items: center;
}
img.img-fluid.ico-pd {
    width: 18px;
    margin-right: 5px;
}
.pd-title {
    color: #00aeef;
    font-size: 16px;
    font-weight: 600;
}
span.pd-gray {
    color: #575759;
    font-size: 16px;
}
.pd-cat {
    position: absolute;
    top: 0;
    right: 0;
    color: #fff;
    background-color: #00aeef;
    margin: 5px;
    padding: 5px;
    border-radius: 5px;
    font-size: 14px;
}
.pd-date {
    color: #00aeef;
    font-size: 14px;
    margin-top: 20px;
}
.pd-body-box {
    padding: 20px;
}
p.pd-desc-txt {
    color: #828186;
    font-size: 15px;
    text-align: justify;
}
article.pd-desc {
    margin: 20px auto;
}
.pd-footer-box {
    background-color: #f1f1f3;
    padding: 20px;
}
.pd-ico-action {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
i.ico-action-item {
    color: #818286;
    font-size: 16px;
    cursor: pointer;
}
hr.hr-pd {
    border-top: 1px solid #959597;
    margin: 10px auto;
}
a.pd-hide-comment {
    color: #9bcc57;
    font-size: 14px;
    margin: 15px auto;
    display: inherit;
}
.pd-user-comment {
    width: 100%;
    flex-wrap: wrap;
    display: flex;
    align-items: center;
}
.pd-user-ava {
    width: 10%;
    margin-right: 5px;
}
.pd-message-wrap {
    width: 85%;
}
img.img-fluid.img-pd-ava {
    width: 70px;
    border-radius: 60px;
}
.pd-username {
    color: #16a7a2;
    font-size: 16px;
}
.pd-mess-cont {
    font-size: 14px;
    color: #625e5f;
}
.pd-comment-box {
    padding: 20px;
    background-color: #ccceda;
}
button.btn.btn-cmt-pd {
    background-color: #fff;
    border: 1px solid #bcbebd;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
}
i.fa.fa-comments-o.btn-cmt {
    color: #a8a9ad;
}
input.form-control.input-cmt {
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}