.circle-four-rb {
    width: 200px;
    height: 200px;
    border: 10px solid #ed1789;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-radius: 100px;
    margin: 0 auto;
}
.ava-box-four {
    font-size: 20px;
    color: #ed1789;
}
.ava-num-four {
    font-size: 18px;
    color: #838488;
}
span.ava-num-four {
    font-size: 18px;
    color: #ed1789;
}
.trans-list-link {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-bottom: 10px;
}
a.trans-link {
    color: #13a69e;
    font-size: 14px;
}