.main-financial-area {
   background-color: #e6e9f0;
}
.financial-box {
   padding: 10px;
   display: flex;
   width: 100%;
}
.nav.flex-column.nav-finance.nav-pills {
   background-color: #ccceda;
   width: 15%;
}
.nav-pills .nav-link.nav-fin-link.active {
    color: #fff;
    background-color: #319ed7;
    border-radius: 0;
    padding: 15px;
    text-align: center;
    font-size: 14px;
}
.nav-pills .nav-link.nav-fin-link {
    color: #fff;
    background-color: transparent;
    border-radius: 0;
    padding: 15px;
    text-align: center;
    font-size: 14px;
}
.tab-content.nav-fin-content {
   width: 85%;
}
.wallet-box {
    margin: 10px;
    background-color: #fff;
}
.wallet-head {
    padding: 10px;
}
h4.finance-title {
    color: #19aea8;
      margin-bottom: 0;
}
.btn-wallet-wrap {
    margin: 10px auto;
}
button.btn.btn-wallet.btn-sm {
    background-image: linear-gradient(to right, #12a89d, #33b747);
    border-color: transparent;
    color: #fff;
    border-radius: 0
}
button.btn.btn-wallet.btn-sm:focus {
    background-image: linear-gradient(to right, #12a89d, #33b747);
    border-color: transparent;
    color: #fff;
    border-radius: 0
}
.fin-att-head {
    display: flex;
    align-items: center;
    background-color: #ccceda;
    padding: 10px;
}
.att-log {
    width: 30px;
    margin-right: 10px;
}
.fin-att-body {
    background-color: #e6e9f0;
    padding: 10px;
}
li.li-att {
    color: #828387;
    font-size: 14px;
}
.tf-history-box {
    margin: 10px;
}
.tf-head {
    background-color: #ccceda;
    padding: 10px;
}
.form-check.label-period {
    padding: 0;
}
th.th-tab-history {
    background-color: #ccceda;
    text-align: center;
    font-size: 14px;
    color: #696d6e;
}
.page-item.disabled .page-link.page-prev {
    color: #fff;
    pointer-events: none;
    cursor: auto;
    background-image: linear-gradient(to right, #ed2025, #fd8f1e);
    border-color: #dee2e6;
}
.page-item .page-link.page-next {
    color: #fff;
    pointer-events: none;
    cursor: auto;
    background-image: linear-gradient(to right, #8dc548, #15a8a0);
    border-color: #dee2e6;
}
a.page-link.pagi-item.active {
    color: #02acea;
}
a.page-link.pagi-item {
    color: #bdbdbf;
}
.card.card-body.collapse-financial {
    background-color: #ccceda;
    border-color: transparent;
}
.dropdown-menu.dropdown-finance.show {
    background-color: #ccceda;
    border-color: transparent;
}
h6.finance-title {
    color: #19aea8;
    margin-bottom: 0;
}
button.btn.btn-topup.btn-sm {
    color: #fff;
    pointer-events: none;
    cursor: auto;
    background-image: linear-gradient(to right, #ed2025, #fd8f1e);
    border-color: #dee2e6;
}
button.btn.btn-withdraw.btn-sm {
    color: #fff;
    pointer-events: none;
    cursor: auto;
    background-image: linear-gradient(to right, #8dc548, #15a8a0);
    border-color: #dee2e6;
}
.action-tab-flex {
    display: flex;
    justify-content: space-between;
}
.circle-one-rb {
    width: 100px;
    height: 100px;
    border: 6px solid #29b15c;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-radius: 60px;
}
.circle-two-rb {
    width: 100px;
    height: 100px;
    border: 6px solid #00a1e2;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-radius: 60px;
}

.circle-three-rb {
    width: 100px;
    height: 100px;
    border: 6px solid #f47820;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-radius: 60px;
}
.ava-box-one {
    font-size: 11px;
    color: #29b15c;
}
.ava-box-two {
    font-size: 11px;
    color: #00a1e2;
}
.ava-box-three {
    font-size: 11px;
    color: #f47820;
}
.ava-num {
    font-size: 10px;
    color: $text-color;
}
span.ava-num-one {
    font-size: 12px;
    color: #29b15c;
}
span.ava-num-two {
    font-size: 12px;
    color: #00a1e2;
}
span.ava-num-three {
    font-size: 12px;
    color: #f47820;
}
.circle-flex-rebate {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}