.setting-box {
    margin: 40px 20px;
}
.set-head-flex {
    display: flex;
    align-items: center;
}
.setting-head {
    margin-bottom: 20px;
}
img.img-fluid.set-ico {
    width: 25px;
    margin-right: 5px;
}
.set-title {
    color: #00aeef;
    font-size: 18px;
    font-weight: 600;
}
.set-nav-tab-wrap {
    display: flex;
    width: 100%;
}
.nav.flex-column.nav-set-det {
    width: 20%;
}
.tab-content.nav-set-content {
   width: 80%;
}
.nav-pills .nav-link.nav-set.active {
    color: #fff;
    background-color: #afb4ba;
    border-radius: 0;
    padding: 15px;
    text-align: center;
    font-size: 16px;
}
.nav-pills .nav-link.nav-set {
    color: #7d868b;
    background-color: #dedce1;
    border-radius: 0;
    padding: 15px;
    text-align: center;
    font-size: 16px;
}
.set-content-tab-box {
    background-color: #fff;
    width: 100%;
    padding: 20px;
}
.sctb-title {
    color: #00aeef;
    font-weight: 600;
}
img.flag-ico {
    width: 20px;
}
label.label-size {
    font-size: 12px;
}
.btn.btn-submit-setting {
    background-image: linear-gradient(to right, #3db24a, #24aadf);
    color: #fff !important;
    margin: 0 5px;
    border-radius: 5px;
    font-size: 16px;
}
.btn.btn-submit-setting:focus {
    background-image: linear-gradient(to right, #3db24a, #24aadf);
}
.button-setting-wraper {
    text-align: center;
    margin: 20px auto;
}
th.ps-size {
    font-size: 14px;
}
.sctb-head {
    margin-bottom: 15px;
}
.table-gradient {
    background-image: linear-gradient(to right, #2d3292, #00adef);
}
.set-table-flex {
    display: flex;
    align-items: center;
    width: 130px;
    justify-content: center;
    margin: 0 auto;
}
.set-img-wraper {
    width: 35%;
    margin-right: 5px;
}
img.img-fluid.img-set-table {
    border-radius: 60px;
}
.block-name {
    color: #ffff;
    font-size: 14px;
    width: 50%;
}
span.td-table-fol {
    text-align: center;
    position: relative;
    top: 10px;
    color: #fff;
}
th.block-center {
    text-align: center;
    color: #fff;
    font-size: 14px;
    font-weight: 600;
}
button.btn.btn-unblock {
    background-color: transparent;
    border: 1px solid #fff;
    color: #fff;
    border-radius: 0;
    margin-top: 5px;
}
.mp-pagination-bc {
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.pc-num-indicator-bc {
    background-color: #fff;
    padding: 5px;
    margin: 5px;
    color: #00aeef;
}