.body-member-area {
   background-color: #e6e9f0;
}
.timeline-box {
    margin: 10px;
}
.update-box {
   background-image: linear-gradient( to right, #3ab54b, #24aadf );
   padding-bottom: 10px;
}
.nav-pills .nav-link.nav-update-item.active{
    color: #fff;
    background-color: transparent;
    border-top: 6px solid #17a84b;
}
.nav-pills .nav-link.nav-update-item{
    color: #fff;
    background-color: transparent;
    border-top: 6px solid #7ac141;
}
.write-update-box {
    background-color: #fff;
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
}
.img-ico-list {
    display: flex;
    align-items: center;
}
img.img-fluid.img-ico {
    width: 15px;
    margin: 5px;
}
.ico-title {
    font-size: 12px;
    color: #5a4a42;
}
.icon-list-item {
    display: flex;
    margin-left: 5px;
}
.btn-list-update-box {
    display: flex;
    justify-content: space-between;
}
button.btn.btn-post {
    background-color: transparent;
    color: #fff;
    border: 2px solid #fff;
    padding: 6px 15px;
    font-size: 12px;
}
.btn-post-wraper {
    margin-right: 10px;
    margin-left: 10px;
}
ul.ul-mid-nav {
    background-color: #fff;
    padding: 10px;
}
li.li-mid-nav {
  list-style-type: none;
  margin: 0;
  padding: 0 5px;
  font-size: 12px;
}
li.li-mid-nav:not(:last-child) {
  border-right: 1px solid #b9bdc0;
}
.nav-pills .nav-link.a-mid-nav.active {
    color: #771377;
    background-color: transparent;
    border-top: 2px solid purple;
    border-radius: 0;
}
.nav-pills .nav-link.a-mid-nav {
    color: #8dc73f;
    background-color: transparent;
    border-top: 2px solid transparent;
    border-radius: 0;
    font-size: 10px;
    padding: 2px;
}
.tl-content-head {
    position: relative;
    background-color: #ccceda;
}
.tl-ct-flex {
    display: flex;
    padding: 10px;
}
.tl-ct-img {
    width: 13%;
    margin-right: 10px;
}
img.img-fluid.img-tl-ct {
    border-radius: 60px;
    border: 2px solid orange;
}
.ct-name {
    color: #2caea4;
    font-weight: 600;
}
.ct-fol, .ct-last-date {
    color: #636467;
    font-size: 12px;
}
.type-ct-box-sp {
    position: absolute;
    top: 0;
    right: 0;
    background-image: linear-gradient(to right, #f26722, #ffaf3e);
    padding: 5px 20px;
    font-size: 12px;
    color: #fff;
    border-bottom-left-radius: 15px;
}
.tl-ct-body-img {
    padding: 20px;
    background-color: #fff;
}
button.btn.btn-action {
    background-color: #fff;
    border: 1px solid #636467;
    font-size: 12px
}
i.act-ico {
    color: #636467;
    font-size: 12px;
}
.tl-ct-action-box {
    display: flex;
    align-items: center;
    background-color: #fff;
    padding-left: 15px;
}
.num-w {
    color: #636467;
    margin: 5px;
    font-size: 12px;
}
.btn-group.btn-group-sm.btn-gr-mr {
    margin: 5px
}
.tl-content-box {
    margin: 10px auto;
}
.tl-content-footer {
    background-color: #fff;
    padding: 10px;
}
a.view-com-ct {
    font-size: 14px;
    text-decoration: none;
    color: #8dc73f;
    margin-left: 10px;
}
form.form-comment-ct {
    margin: 10px;
}
.tl-ct-body-img-art {
    background-color: #fff;
    padding: 10px;
}
.tl-ct-body-img-wrap {
    background-color: #f3f3f5;
    padding: 10px;
    border-radius: 5px;
}
h5.tl-news-title {
    color: #838488;
    margin-bottom: 15px;
}
p.tl-art-desc {
    color: #838488;
    text-align: justify;
    font-size: 14px;
}
.commentator-box {
    position: relative;
}
.commentator-flex {
    display: flex;
    padding: 10px 20px 0;
}
.cmt-img {
    width: 10%;
    margin-right: 10px;
}
img.img-fluid.img-cmt {
    border-radius: 60px;
}
.cmt-name {
    color: #2caea4;
    font-weight: 600;
}
p.p-cmt-mess {
    font-size: 14px;
    text-align: justify;
}
.cmt-mess {
    position: relative;
}
.cmt-date {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 12px;
    color: $text-color;
}
hr.hr-cmt {
    border-top: 2px solid $text-color;
    margin: 0 auto;
}
.tl-ct-body-stat {
    background-color: #fff;
    padding: 20px;
}
.type-ct-box-art {
    position: absolute;
    top: 0;
    right: 0;
    background-image: linear-gradient(to right, #7d64a8, #04a8ef);
    padding: 5px 20px;
    font-size: 12px;
    color: #fff;
    border-bottom-left-radius: 15px;
}
.type-ct-box-st {
    position: absolute;
    top: 0;
    right: 0;
    background-image: linear-gradient(to right, #ec2079, #c21f32);
    padding: 5px 20px;
    font-size: 12px;
    color: #fff;
    border-bottom-left-radius: 15px;
}
.share-article-box {
    background-color: #fff;
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
}
label.label-share {
    font-size: 14px;
    color: $text-color;
    margin-left: 5px;
}
.ads-banner-img-wraper {
    position: relative;
}
.close-ads {
    position: absolute;
    top: 0;
    right: 0;
    background-color: rgba(128, 128, 128, .8);
    padding: 5px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
}
.label-video-input {
    cursor: pointer;
    margin-bottom: 0;
}