.shop-box {
   margin: 40px 20px;
}
.shop-head {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    background-color: #fff;
    padding: 20px;
}
.shop-unit {
    background-color: #fff;
    padding: 20px;
    margin-bottom: 20px;
}
a.shop-link {
    color: #00aeef;
    font-size: 14px;
}
.shop-price {
    color: #29b973;
    font-weight: 600;
}
.shop-desc {
    font-size: 14px;
    color: #828284;
}
span.shop-green {
    color: #29b973;
}
td.shop-det-col {
   color: #59585d;
   border-top-color: transparent;
}
table.table.table-shop-orice {
    margin-top: 60px;
}
.shop-desc-title {
    color: #59585d;
    font-weight: 600;
    margin-bottom: 25px;
}
.shop-deta-img-wrap {
    border: 1px solid #00aeef;
}