/* PLACEHOLDERS */

%img-wraper {
  position: relative;
  overflow: hidden;
  background-color: #fff;
  width: 100%;
  padding-top: 100%;
  img {
    position: absolute;
    margin: auto;
    min-height: 100%;
    min-width: 100%;
    left: -100%;
    right: -100%;
    top: -100%;
    bottom: -100%;
  }
}

%box-shadow {
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.15);
}
$text-color: #838488;
